
.projectDetails img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  margin-top: 60px;
  box-shadow: 0px 0px 50px 25px rgb(122, 122, 122);
}
.project1 {  
  width: 70%;
  height: 3000%;
}
.project2 {  
  width: 80%;
  height: 3000%;
}
.project3 {  
  width: 60%;
  height: 600px;
}
.project4 {  
  width: 40%;
  height: 600px;
}

.containProject {
  display: grid;
  grid-template-columns: repeat(12, 100px);
  grid-template-rows: repeat(14,20px);
  margin-left: auto;
  margin-right: auto;
  height: 500px;
  width: 1200px;
  /* background-color: #ffffff;
  border-radius: 10%;
  box-shadow: 0px 0px 10px 5px #86679a; */
  color:#5d5c5c;
}
.containProject h1{
  grid-row-start: 3;
  grid-column-start: 2;
  grid-column-end: 10;
  font-weight:bold;
  font-size: 300%;  
}
.containProject h2{
  font-family: sans-serif;
  font-weight:700;
  font-size: 100%;  
  color: rgb(33, 33, 33);
}
.containProject p{
  font-size: 120%;  
  font-weight: 500;
}
.containProject .roleDate{
  grid-row-start: 7;
  grid-column-start: 2;
  grid-column-end:10;
  font-size: 120%;  
  font-weight: 500;
}
.containProject .body{
  grid-row-start:10;
  grid-column-start: 2;
  grid-column-end:7;
  font-size: 120%;  
  font-weight: 500;
} 
.skills{
  grid-row-start: 14;
  grid-column-start: 8;
  grid-column-end:10;
  line-height: 30px;

}
.role{
  grid-row-start:10;
  grid-column-start: 8;
  grid-column-end:10;
  line-height: 30px;

} 
.collabP{
  grid-row-start:10;
  grid-column-start: 11;
  line-height: 30px;
} 
.visual iframe{
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0px 0px 20px 10px rgb(122, 122, 122);
  margin-bottom: 80px;
  border-radius: 40px;
}
.border {
  height: 2px;
  width: 1200px;
  margin-bottom: 70px;
  margin-left: auto;
  margin-right: auto;
  background-color: #555;
}
.title h1{
  text-align: center;
  padding-bottom: 70px;
  font-weight:bold;
  font-size: 300%;  
  color:#5d5c5c;
}