
*{
  font-family: 'Quicksand';
}
.consultingDetails img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  margin-top: 60px;
  box-shadow: 0px 0px 50px 25px rgb(122, 122, 122);
}
.consulting1 {  
  width: 80%;
  height: 3000%;
}
.consulting2 {  
  width: 80%;
  height: 3000%;
}
.consulting3 {  
  width: 69%;
  height: 600px;
}

.containConsulting {
  display: grid;
  grid-template-columns: repeat(12, 100px);
  grid-template-rows: repeat(14,20px);
  margin-left: auto;
  margin-right: auto;
  height: 500px;
  width: 1200px;
  /* background-color: #ffffff;
  border-radius: 10%;
  box-shadow: 0px 0px 10px 5px #86679a; */
  color:#5d5c5c;
}
.containConsulting h1{
  grid-row-start: 3;
  grid-column-start: 2;
  grid-column-end: 12;
  font-weight:bold;
  font-size: 300%;  
}
.containConsulting h2{
  font-family: sans-serif;
  font-weight:700;
  font-size: 100%;  
  color: rgb(33, 33, 33);
}
.containConsulting p{
  font-size: 120%;  
  font-weight: 500;
}
.containConsulting .roleDate{
  grid-row-start: 7;
  grid-column-start: 2;
  grid-column-end:10;
  font-size: 120%;  
  font-weight: 500;
}
.containConsulting .body{
  grid-row-start:10;
  grid-column-start: 2;
  grid-column-end:7;
  font-size: 120%;  
  font-weight: 500;
} 
.collabC{
  grid-row-start:10;
  grid-column-start: 11;
  line-height: 30px;
} 