@import url('https://fonts.googleapis.com/css2?family=Hedvig+Letters+Serif:opsz@12..24&display=swap');

*{
  font-family: 'Hedvig Letters Serif', serif;
}
.container {
  display: grid;
  grid-template-columns: repeat(10,100px);
  grid-template-rows: repeat(40,10px);
  margin: auto;
  margin-top: 50px; 
  height: 400px;
  width: 1000px;
  /* background-color: #ffffff; */
}
.info{
  color:#0d0d0d;
  grid-column-start: 2;
  grid-column-end: 8;
  grid-row-start: 20;
  z-index: 2;
}
.info h1{
  font-weight: 1000;
  font-size: 360%;  
}
.info p{
  padding-top: 10px;
  font-size: 120%;  
}
.pfp {  
  grid-column-start: 8;
  grid-column-end: 10;
  grid-row-start: 9;
  font-weight: 1000;
  z-index: 2;
  width: 120%;
  border-radius: 150px;
  border: 5px solid #384b2d;  
}

.icons {  
  color: #86679a;
  grid-column-start: 4;
  grid-column-end: 7;
  grid-row-start: 35;
  z-index: 2;
}
.container1 {
  display: inline-flex;
  justify-content: center;
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 2;
  grid-template-columns: 200px 200px 200px 100px 100px;
  grid-template-rows: 30px 20px;
  width: 30px;
  height: 30px;
  padding: 10px;
  border-radius: 10%;
  box-shadow: 3px 10px 3px rgb(153, 153, 153);
  background-color: #ffffff;
  margin-right: 30px;
}
.container1:hover{
  background-color: #637d55;
}

